<template>
  <div class="shipmentSearch">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        出貨單紀錄查詢
        <!-- <v-img :src= "logoImage" aspect-ratio="2.5" contain></v-img> -->
        <v-spacer></v-spacer>
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model = "searchKey"
          ></v-text-field>

        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left">日期</th>
                    <th class="text-left">單據號碼</th>
                    <th class="text-left">客戶統編/名稱</th>
                    <th class="text-left">指定送貨地址</th>
                    <th class="text-left">品名</th>
                    <th class="text-left">規格</th>
                    <th class="text-left">數量</th>
                    <th class="text-left">公斤</th>
                    <th class="text-left">單價</th>
                    <th class="text-left">單價調整</th>

                    <th class="text-left">買賣金額</th>
                    
                    <th class="text-left">沖鎖金額</th>
                    <th class="text-left">沖鎖調整</th>
                    <th class="text-left">折工金額</th>
                    <th class="text-left">折工調整</th>
                    <th class="text-left">加工金額</th>
                    <th class="text-left">加工調整</th>

                    <th class="text-left">備註</th>
                    <th class="text-left">內部備註內容</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <!-- <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="">
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.shipment_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.shipment_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td> -->
                    
                    <td>{{ item.date }}</td>
                    <td>{{ item.code }}</td>
                    <td>{{ item.customer.text }}</td>
                    <td>{{ item.otheraddress }}</td>
                    <td>{{ item.rawmaterial.thickness + item.rawmaterial.name }}</td>
                    <td>
                      <div
                        class="d-flex justify-space-between align-center"
                        style="width: 250px"
                      >
                        <span>{{ item.width }} * {{ item.long }}</span>
                        
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/punchlock/' + item.punchlock.picname"
                          v-if="item.punchlock"
                        ></v-img>
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/fold/' + item.fold.picname"
                          v-if="item.fold"
                        ></v-img>
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/machining/' + item.machining.picname"
                          v-if="item.machining"
                        ></v-img>
                      </div>
                    </td>
                    <td>
                      {{ item.quantity }}
                    </td>
                    <td>{{ filterweight(item) }}</td>
                    
                    <td>
                      {{
                        parseInt(item.rawmaterial.price) +
                        parseInt(item.extraRawmaterialPrice)
                      }}
                    </td>
                    <td>
                      <!-- 單價調整 -->
                      {{item.extraRawmaterialPrice}}
                      
                    </td>

                    <td>{{ filterFinalPrice(item) * item.quantity }}</td>
                    <td>
                      <!-- 沖鎖金額 -->
                      <div v-if="item.punchlock">
                        {{ (parseInt(item.punchlock.price) + parseInt(item.extraPunchlockPrice) * item.quantity) }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 沖鎖調整 -->
                      {{item.extraPunchlockPrice}}
                    </td>
                    <td>
                      <!-- 折工金額 -->
                      <div v-if="item.fold">
                        {{ (parseInt(item.fold.price) + parseInt(item.extraFoldPrice)) * item.quantity }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 折工調整 -->
                      {{item.extraFoldPrice}}
                    </td>
                    <td>
                      <!-- 加工金額 -->
                      <div v-if="item.machining">
                        {{ (parseInt(item.machining.price) + parseInt(item.extraMachiningPrice)) * item.quantity }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 加工調整 -->
                      {{item.extraMachiningPrice}}
                    </td>
                    
                    <td>
                      {{item.note}}
                    </td>
                    <td>
                      {{item.insideNote}}
                    </td>
                    
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>

export default {
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      logoImage:require('@/assets/img/tosyokan_book_kensaku_woman.png'),
      picBaseUrl: this.$store.state.base_url + "/upload/shipment/",
      selectedFile:"",
      uploadItem:"",
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";
   
      return  keys.reduce(function(prev, element) {
        return prev.filter(item => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
      
    }
  },

  methods: {
    getAllData() {
      //取得所有資料
      this.myAxios("general/getAll/" + this.$store.state.databaseName + "/shipment")
        .then((rs) => {
          this.items = rs.reduce((finalItems,i) => {
            let newItem = JSON.parse(i.datalist);
            newItem.tempItems.forEach(tempItem=>{
              finalItems.push(tempItem)
            });
            return finalItems;
          },[]);
        });
    },
     //重量計算
    filterweight(item) {
      let val =
        item.rawmaterial.thickness *
        (item.width / 1000) *
        (item.long / 1000) *
        7.93 *
        item.quantity;
      return (Math.round(val*10)/10);  //2021.3.19 改回傳四捨五入,取小數點一位
    },
    //最終單價計算
    filterFinalPrice(item) {
      let val =
        item.weight *
        (parseInt(item.rawmaterial.price) +
          parseInt(item.extraRawmaterialPrice));
      return Math.round(val);  //2021.3.19 改回傳四捨五入,取整數
    },
  },
};
</script>


<style lang="scss">
.shipmentList {
  .theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    font-size: 1rem;
  }
}
</style>